<template>
  <div class="container py-5">
    <div>
      <h1>{{ restaurant.name }}</h1>
      <span class="badge badge-secondary mt-1 mb-3">{{ restaurant.categoryName }}</span>
    </div>

    <hr />

    <ul>
      <li>評論數： {{ restaurant.commentsLength }}</li>
      <li>瀏覽次數： {{ restaurant.viewCounts }}</li>
    </ul>

    <button type="button" class="btn btn-link" @click="$router.back()">回上一頁</button>
  </div>
</template>

<script>
import restaurantsAPI from "../apis/restaurants";
import { Toast } from "../utils/helpers";

export default {
  data() {
    return {
      restaurant: {
        name: "",
        categoryName: "",
        commentsLength: 0,
        viewCounts: 0
      }
    };
  },
  methods: {
    async fetchRestaurant(restaurantId) {
      try {
        //TODO: 向後端GET資料
        const { data } = await restaurantsAPI.getRestaurant({ restaurantId });
        if (data.status === "error") {
          throw new Error(data.message);
        }

        const { name, Category, Comments, viewCounts } = data.restaurant;
        this.restaurant.name = name;
        this.restaurant.categoryName = Category.name;
        this.restaurant.commentsLength = Comments.length;
        this.restaurant.viewCounts = viewCounts;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error
        });
      }
    }
  },
  created() {
    const { id: restaurantId } = this.$route.params;
    this.fetchRestaurant(restaurantId);
  }
};
</script>